@import "../abstracts/global";

// =====================================>   Home Section     <================================

.plan {
  @include LightComponet;
  // early changing paddings
  @media screen and (max-width: 670px) {
      padding: 40px 25px;
  }
.wrapper-rounded-step {
  display: none;
}

.h2-div {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
}

h2 {
  @include h2-text;
  border-color: $main-darkblue;
}

.wrapper-simple-step {
  text-align: center;
  @include step-card;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  .step {
    margin: 10px;
    max-width: 300px;
    width: 220px;
  }
}

// Media screen for showing rounded and making simple display
@media screen and (min-width: 835px) {
  .wrapper-simple-step {
    display: none;
  }
  .wrapper-rounded-step {
    @include rounded-step-card;
    display: block;
  }
}

}
