@import "variables";

//================================ Component section
@mixin Component {
  padding: 40px 80px 80px;
  @media screen and (max-width: 500px) {
      padding: 40px 25px;
  }
}


@mixin DarkComponent {
  @include Component;
  background-color: $main-darkblue;
}

@mixin LightComponet {
  @include Component;
  background-color: $main-lightblue
}

// =================>   Logo section
@mixin Big-logo {
  width: 80px;
  font-size: 13px;
   img {
    width: 76%;
    height: auto;
    object-fit: contain;
    margin: 0px 12%;
  }
  .first {
    font-size: 0.9em;
    margin: 1px 0px 2px 12%;
    letter-spacing: 0.2px;
  }
  .second {
    font-size: 0.67em;
    margin: 0px;
  }
}

@mixin Small-Logo {
  // Logo
  .logo-div {
    width: 55px;
    font-size: 12px;
   img {
     width: 100%;
     margin: 0px;
   }
   .first {
     margin-left: 0px;
   }
   .second {
     font-size: 0.5em;
   }
  }
}

//=========== Burger
@mixin Burger {
  div {
    margin: 4px;
    width: 28px;
    height: 4px;
    border-radius: 2px;
    background-color: $main-font-color;
  }
}


//================================ Button section
@mixin Button {
  background-color: $body-back-color;
  color: inherit;
  letter-spacing: 0.5px;
  word-spacing: 2px;
  font-family: $font-SegoeProLight;
  font-size: 0.9rem;
  box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.22);

  &:hover {
    background-color: $button-hover;
    box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.22);
  }
  &:active {
    background-color: $button-active;
    box-shadow: 0px 4px 25px 0px rgba(0, 0, 0, 0.22);
  }
}

// ========================== blue Round section
@mixin Dark-round-div {
  border-radius: 50%;
  background-color: $main-darkblue;
}
@mixin Light-round-div {
  border-radius: 50%;
  background-color: $main-lightblue;
}

//======================Header anchors section
@mixin A-links {
  & a {
    display: inline-block;
    padding: 5px 15px;
    color: inherit;
    text-decoration: none;
  }
}

@mixin h1-p-header {
  h1 {
    font-size: 2.3em;
    margin-top: 0px;
    margin-bottom: 1.4em;
    width: 6em;
    span {
      color: $main-lightblue;
    }
  }
  p {
    font-size: 1em;
    margin-bottom: 2.5em;
    width: 18em;
    max-width: 100%;
    opacity: 0.95;
  }
}


// ==================== Plan section
@mixin h2-text {
  font-family: $font-OpenSans-Semibold;
  margin: 0px;
  display: inline-block;
  padding: 12px 30px;
  font-size: 18px;
  border: 1px solid;
  border-radius: 2px;
}

@mixin step-card {
  & a {
    text-decoration: none;
    color: inherit;
  }
  .step-img-div {
    margin: 15px auto;
    width: 160px;
    height: 160px;
    @include Dark-round-div;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .step-img-div div {
    margin-bottom: 20px;
  }
  .step-img-div p {
    margin-bottom: 20px;
  }
  .step-img-div .inside {
    font-family: $font-SegoeProLight;
  }
  .semi-italic {
    font-family: $font-SegoeProSemilightItalic;
  }
  .bold-italic {
    font-family: $font-SegoePro-BoldItalic;
  }
  & a {
    font-family: $font-SegoePro-LightItalic;
  }
  & span {
    border-bottom: 1px solid #FFFFFF;
  }
  & i {
    margin-left: 4px;
    color: #223E73;
  }
}




@mixin rounded-step-card {
  .main {
    width: 358px;
    height: 358px;
    border: 3px solid #FFFFFF;
    border-radius: 180px;
    margin: 160px auto;
    position: relative;
  }
  .step {
    @include step-card;
    .step-text-div {
      text-align: right;
      position: absolute;
      width: 300px;
      z-index: 2;
    }
    .step-img-div {
      position: absolute;
      z-index: 3;
    }
  }
  .inside {
    text-align: center;
  }

  // R = 180px r = 80px
  .one .step-img-div {
    // Yuqoridagi
    top: -80px;
    right: 100px;
  }
  .two .step-img-div{
    // right
    bottom: 10px;
    right: -55px;
  }
  .three .step-img-div{
    // left
    bottom: 10px;
    left: -55px;
  }



  .one .step-text-div {
    right: 245px;
  }
  .two .step-text-div {
    top: 180px;
    left: 0px;
  }
  .three .step-text-div {
    top: 180px;
    right: 20px;
  }

  @media screen and (max-width: 1100px){
    .step {
      .step-text-div{
        width: 254px;
      }
    }
      .one .step-text-div {
        right: 225px;
      }
      .two .step-text-div {
        left: -20px;
      }

  // end of media
  }
}
