@font-face {
  font-family: 'Open-Sans-Light';
  font-weight: 300;
  src: local("Open Sans Light"), local("OpenSans-Light"), url("./fonts/OpenSans-Light.ttf");
}

@font-face {
  font-family: 'Open-Sans-Light-Italic';
  font-weight: 300;
  font-style: italic;
  src: local('Open Sans Light Italic'), local('OpenSans-LightItalic'),
       url('./fonts/OpenSans-LightItalic.ttf');
}

@font-face {
  font-family: 'Open-Sans-Semibold';
  font-weight: 600;
  src: local('Open Sans Semibold'), local('OpenSans-Semibold'),
       url('./fonts/OpenSans-Semibold.ttf');
}


// ======================================>>>>>>>>> SegoePro <<<<<<<<<<<<===============================================
@font-face {
  font-family: 'SegoeProDisplay-Light';
  font-weight: 300;
  src: local('SegoePro Light'), local('SegoePro-Light'),
       url('./fonts/SegoeProDisplay-Light.woff');
}

@font-face {
  font-family: 'SegoePro-SemilightItalic';
  font-weight: 300;
  font-style: italic;
  src: local('SegoePro SemilightItalic'), local('SegoePro-SemilightItalic'),
       url('./fonts/SegoePro-SemilightItalic.woff');
}

@font-face {
  font-family: 'SegoePro-LightItalic';
  font-weight: 300;
  font-style: italic;
  src: local('SegoePro LightItalic'), local('SegoePro-LightItalic'),
       url('./fonts/SegoePro-LightItalic.woff');
}

@font-face {
  font-family: 'SegoePro-BoldItalic';
  font-weight: 700;
  font-style: italic;
  src: local('SegoePro BoldItalic'), local('SegoePro-BoldItalic'),
       url('./fonts/SegoePro-BoldItalic.woff');
}

