@import "./abstracts/global";
@import "./sections/global";


body {
  font-size: 16px;
  padding: 0px $left-right;
  overflow-x: hidden;
  background-color: $body-back-color;
  color: $main-font-color;
  font-family: $font-OpenSans-Light;
}

.topShadow {
  background-color: $main-lightblue;
  height: $top-bottom;
  transform: translateX(24px) skew(-44deg);
  overflow-x: hidden;
}

.bottomShadow {
  background-color: $main-darkblue;
  height: $top-bottom;
  transform: translateX(24px) skew(44deg);
  overflow-x: hidden;
}

@media screen and (max-width: 1015px) {
  body {
    padding: 0px;
    background-color: white;
  }
  .bottomShadow,
  .topShadow {
    display: none;
  }
}

img {
  max-width: 100%;
}











