@import "../abstracts/global";

// =====================================>   Home Section     <================================

$header-big-margins: 80px 40px 0px;

.home {
  @include DarkComponent;
}
  // =================== Header
.home header {
  @include h1-p-header;
  padding: $header-big-margins;
  button {
    @include Button;
    padding: 12px 42px;
    border-radius: 22px;
    border: none;
    outline: none;
  }
  .home-pic-div {
    @include Light-round-div;
    margin-top: 30px;
    margin-left: auto;
    max-width: 330px;
  }
}


  //================ Nav
.home  nav {
  display: flex;
  align-items: center;
  @include A-links;
  .links {
    margin: $centering-margins;
  }
  .logo-div {
    @include Big-logo;
    cursor: pointer;
  }
  .burger {
    display: none;
    @include Burger;
  }

  //search
   .search {
    padding-right: 25px;
  }
   .search input {
     display: none;
  }
  .search i {
    padding: 5px;
    cursor: pointer;
    font-size: 1.4rem;
  }

}
