@media screen and (min-width: 650px) {

  .home header {
    display: flex;
    justify-content: space-around;

    .home-pic-div {
      margin: 0px;
      align-self: center;
      margin-bottom: 35px;
    }
  }
}



// ============== Navigation change
@media screen and (max-width: 935px) {
  // re declaring header's padding
  .home header {
    padding-left: 0px;
    padding-right: 0px;
    .home-pic-div {
      max-width: 280px;
    }
  }

// Navigation
  .home nav {
    @include Small-Logo;
    justify-content: space-between;
    .burger {
      display: block;
    }
    .search,
    .links {
      display: none
    }

  }
// end of media query
}


@media screen and (max-width: 750px) {
  .home header {
    font-size: 14px;
  }
}
